
.inner-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #ff4b33;
    max-width: 75%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 120px;
    border-radius: 20px;
}

.flex-row {
    display: flex;
    flex-direction: row;
    column-gap: 15px;
}

button {
    margin-bottom: 25px;
    margin-top: 12px;
    width: 240px;
}

.verify-button:hover {
    background-color: rgb(230,230,230);
}

button:hover {
    background-color: white;
    color: #a63121
}

h2 {
    color: #fff;
    font-size: 32px
}

.inner-container, p {
    margin-top: 0px;
    color: #fff;
    font-size: 24px;
    font-weight: bold;
}

.verify-container, p {
    color: black;
    font-weight: normal;
    padding-left: 20px;
    text-align: left;
}

.verify-container, h2 {
    color: black;
    font-weight: normal;
    text-align: center;
}

.verify-container {
    margin-top: 20px;
    border: 0.5px lightgray solid;
    min-width: 480px;
    width: 50%;
    min-height: 350px;
    max-height: 60vh;
    margin-left: auto;
    margin-right: auto;
    
}

